<template>
    <div class="progress-div" v-if="!visibleTip">
        <div class="middle">
            <div class="topWrap">
                <img class="waitImg" :src="img" alt="">
                <div class="timeWrap">{{time}}</div>
            </div>

            <div class="process-static">
                <div class="process-active" :style="{width:percent+'%'}"></div>
            </div>
            <p class="text">
                {{ waitText }}
            </p>
        </div>
    </div>
</template>

<script>
import waitCard from '../assets/images/wait_card.png';
import { mapMutations, mapState } from 'vuex';
import audioDefault from '@/assets/media/common/15.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'Process',
    props: {
        audio: { default: '' },
        goToRoute: { type: String },
        type: { type: Number },
        img: { default: waitCard },
        waitText: { default: '正在处理中, 请耐心等待......' },
        percent: { default: 100 },
        handleBusiness: { default: null }
    },
    data () {
        return {
            time: 1,
            intervalId: null
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep']),
        startTime () {
            this.time = 1;
            if (this.intervalId) {
                clearInterval(this.intervalId);
            }
            this.intervalId = setInterval(() => {
                this.time++;
            }, 1000);
        }
    },
    computed: {
        ...mapState('common', {
            visibleTip: 'visibleTip'
        })
    },
    beforeDestroy () {
        this.audioObj.destroy();
        clearInterval(this.intervalId);
    },
    watch: {
        type () {
            if (this.audioObj) {
                this.audioObj.destroy();
                this.audioObj = null;
            }
            this.audioObj = playAudio(this.audio || audioDefault);
            this.audioObj.play();

            if (this.handleBusiness) {
                this.startTime();
                this.handleBusiness();
            } else {
                this.changeStep('down');
            }
        },
        visibleTip () {
            if (!this.visibleTip) {
                if (this.goToRoute) {
                    this.changeStep(this.goToRoute);
                } else {
                    this.changeStep('up');
                }
            }
        }
    },
    mounted () {
        this.audioObj = playAudio(this.audio || audioDefault);
        this.audioObj.play();
        if (this.handleBusiness) {
            this.startTime();
            this.handleBusiness();
        } else {
            this.changeStep('down');
        }
    }
};
</script>

<style lang="scss" scoped>
    .progress-div {
        position: fixed;
        z-index: 99;
        height: 100%;
        width: 100%;
        background: rgba(0, 0, 0, 0.7);
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;

        .middle {
            text-align: center;
            position: relative;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            .process-static {
                width: 400px;
                height: 30px;
                background: #659ED9;
                border-radius: 10px;
                border: 1px solid #236FA8;
                margin-top: 10px;
                position: relative;
            }

            .text {
                color: #fff;
                margin-top: 20px;
                font-size: 20px;
            }
        }
    }

    .waitImg {
        opacity: .5;
    }

    .topWrap {
        display: flex;
    }

    .timeWrap {
        flex: 1;
        font-size: 100px;
        color: #ffffff;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .process-active {
        position: relative;
        top: 0;
        left: 0;
        width: 0;
        height: 30px;
        border: 1px solid #236FA8;
        border-radius: 10px;
        background-image: linear-gradient(60deg, transparent 0rem, transparent 0.4rem, #236FA8 0.4rem, #236FA8 0.8rem, transparent 0.8rem, transparent 1.2rem, #236FA8 1rem);
        background-color: #1265A2;
        background-size: 20px 38px;
        box-shadow: 0 0 10px 3px #76BAEB;
        box-sizing: border-box;
        -webkit-animation: process 800ms infinite linear;
        animation: process 800ms infinite linear;
        transition: width 1s;
    }

    .process-active:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        height: 100%;
        border-radius: 10px;
        background-image: linear-gradient(to bottom, rgba(0, 140, 213, .6), rgba(0, 140, 213, .6) 15%, transparent 60%, rgba(0, 140, 213, .6));
    }

    /* 动画 */
    @-webkit-keyframes process {
        0% {
            background-position: 0 0;
        }
        100% {
            background-position: 20px 0;
        }
    }

    @keyframes process {
        0% {
            background-position: 0 0;
        }
        100% {
            background-position: 20px 0;
        }
    }

</style>
