<template>
    <div>
        <div class="kd-page-title">
            请选择用途
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <el-form
                class="uploadLabel"
                label-width="15vw"
            >
                <el-form-item label="名称：">
                    <el-radio-group v-model="useType">
                        <el-radio
                            class="uploadChoose"
                            v-for="(item,index) in usage"
                            :key="index"
                            :label="item.type"
                            border
                        >
                            {{ item.value }}
                        </el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-form>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goNext"
                :but-disable="!useType"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import audioObj from '@/assets/media/household/11.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'NoCrimeHefeiCancelReason',
    computed: {
        ...mapState('innocent', {
            usage: 'usage'
        })
    },
    data () {
        return {
            useType: this.$store.state.innocent.useType
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('innocent', ['setUseType']),
        goUp () {
            this.changeStep('up');
        },
        async goNext () {
            this.setUseType(this.useType);
            this.changeStep('down');
        }
    },
    mounted () {
        this.audioObj = playAudio(audioObj);
        this.audioObj.play();
    },
    beforeDestroy () {
        this.audioObj.destroy();
    }
};
</script>

<style lang="scss" scoped>
    .bg {
        width: 34.37vw;
        height: 26.56vh;
        border-radius: 0.5vh;
        background: url("../../../../../assets/images/cardno_bg.png");
        background-size: 100% 100%;
        margin: 0 auto;
    }

    .wrap-img {
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 400px;
            height: 300px;
            background: #fff;
        }
    }

    .wrap-info {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        padding-top: 5vw;

        ::v-deep .el-form-item__content {
            margin-right: 10vw;
        }

        .uploadLabel .uploadChoose {
            width: auto !important;
            margin-bottom: 2vw;

        }

        .uploadButton {
            background: rgba(255, 255, 255, 0.8);
            border-radius: 6px;
            border: 1PX solid rgba(242, 249, 255, 1);/*no*/
            height: 13vw;
            text-align: center;

            p {
                font-size: 5vh;
                color: #85BAF0;
                text-align: center;
                line-height: 13vw;
            }
        }
    }
</style>
