<template>
    <div>
        <div class="kd-page-title">
            手机校验
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <verify-code
                @getVerifyCode="getVerifyCode"
                ref="verifyCode"
            />
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="提交"
                :button-handle="goNext"
            />
        </div>
    </div>
</template>

<script>
import verifyCode from '@/components/verify-code';
import { mapState, mapMutations } from 'vuex';
import { pushProve } from '../../../../../service/human/prove';
import { trafficGetVerify, trafficVerifyCode } from '@/service/traffic/traffic';

export default {
    name: 'NoCrimeHefeiCheckPhone', // 手机校验
    components: { verifyCode },
    computed: {
        ...mapState('innocent', {
            useType: 'useType'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('innocent', ['setInnocentCompleteRes']),
        goUp () {
            this.changeStep('up');
        },
        async getVerifyCode () {
            const res = await trafficGetVerify({
                phone: this.$refs.verifyCode.item.phoneNumber,
                serviceType: 2
            });
            if (res && res.code === '0') {
                console.log('获取验证码成功');
            } else if (res) {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        },
        async goNext () {
            if (this.$refs.verifyCode.submission()) {
                const res = await trafficVerifyCode({
                    phone: this.$refs.verifyCode.item.phoneNumber,
                    code: this.$refs.verifyCode.item.verifyCode,
                    serviceType: 2
                });
                if (res && res.code === '0') {
                    const res = await pushProve({
                        usage: this.useType,
                        phone: this.$refs.verifyCode.item.phoneNumber
                    });
                    if (res && res.code === '0') {
                        this.setInnocentCompleteRes(res.result);
                        this.changeStep('down');
                    } else if (res) {
                        this.changeTipPage({
                            message: res.message,
                            disableBack: true
                        });
                    }
                } else if (res) {
                    this.changeTipPage({
                        message: res.message,
                        enableBack: true
                    });
                }
            }
        }
        // async goNext () {
        // 	if (this.$refs.verifyCode.checkPhoneExistence()) {
        // 		let res = await pushProve({
        // 			usage: this.useType,
        // 			phone: this.$refs.verifyCode.item.phoneNumber
        // 		});
        // 		if (res && res.code === '0') {
        // 			this.setInnocentCompleteRes(res.result);
        // 			this.changeStep('down');
        // 		 } else if (res) {
        // 			this.changeTipPage({message: res.message, disableBack: true});
        // 		}
        // 	}
        // }
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        justify-content: center;
        align-items: center;
    }
</style>
