<template>
    <div>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'precheck')"
            :handle-business="checkHandle"
        />
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process
            :type="2"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
        />
        <reason v-if="currentStep === steps.findIndex(step=>step.key === 'reason')"/>
        <check-phone v-if="currentStep === steps.findIndex(step=>step.key === 'checkPhone')"/>
        <process
            :type="3"
            v-if="currentStep === steps.findIndex(step=>step.key === 'print')"
            :handle-business="submitProve"
            :wait-text="processText"
        />
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            :data="innocentCompleteRes"
            :foot-texts="prompt"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import process from '@/components/process';
import checkPhone from './components/check-phone';
import reason from './components/cancel-reason';
import complete from '@/components/complete';
import { noCrimeProveFace, proveCrimeComplete, noCrimeProveCheck } from '@/service/human/prove';
import { toPrint } from '@/service/common';

export default {
    name: 'NoCrimeHefeiIndex', // 无犯罪记录证明
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        process,
        checkPhone,
        complete,
        reason
    },
    data () {
        return {
            processText: '',
            prompt: '温馨提示：公安户政部门将会对提交信息进行审核，审核结果将以短信告知，若审核通过，请携带身份证前往原自助设备刷身份证领取证明报告。'
        };
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            binocularCamera: 'binocularCamera',
            businessTypeCode: 'businessTypeCode'
        }),
        ...mapState('innocent', {
            printView: 'printView', // 打印链接
            innocentCompleteRes: 'innocentCompleteRes'
        })
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        ...mapMutations('innocent', ['setPrintView', 'setUsage', 'setInnocentCompleteRes']),
        async checkHandle () {
            const res = await noCrimeProveCheck({
                businessType: this.businessTypeCode,
                idNum: this.identifyCard.certNumber
            });
            if (res && res.code === '0') {
                if (res.result.status === 3) { // 审核不通过
                    this.changeTipPage({
                        message: '审核不通过，请重新办理',
                        enableBack: true
                    });
                    this.changeStep('down');
                } else if (res.result.status === 2) { // 正在审核
                    this.changeTipPage({ message: res.result.message });
                } else { // 未提交、审核通过
                    this.changeStep('down');
                }
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        goPreStep () {
            this.changeStep('face-yitu');
        },
        async handleBusiness () {
            const res = await noCrimeProveFace({
                idNum: this.identifyCard.certNumber,
                identifiedPhoto: this.binocularCamera
            });
            if (res && res.code === '0') {
                // this.setProveId(res.result.noCriminalProvingId);
                if (res.result.status === 2 && res.result.nowStatus === 2) { // 已提交且通过
                    this.setPrintView(res.result.printLink);
                    this.changeStep('print');
                } else { // 未提交
                    this.setUsage(res.result.usages);
                    this.changeStep('down');
                }
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async submitProve () {
            // 申请完成
            if (this.printView) { // 说明是打印
                const res = await toPrint({
                    path: this.printView
                });
                if (res && res.code === 1) {
                    this.completeProve();
                } else {
                    this.changeTipPage({ message: '打印机出故障啦，请稍后重试' });
                }
            } else {
                this.processText = '正在提交中，请稍等';
                this.changeStep('down');
            }
        },
        async completeProve () {
            // 办理完成
            const res = await proveCrimeComplete({});
            if (res && res.code === '0') {
                this.setInnocentCompleteRes(res.result);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }
    },

    mounted () {
        this.setSteps([
            {
                name: '身份识别',
                isActive: true,
                visible: true,
                key: 'identifyCardPage'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'precheck'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'faceYitu'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'contrast'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'process'
            },
            {
                name: '选择原因',
                isActive: false,
                visible: true,
                key: 'reason'
            },
            {
                name: '手机校验',
                isActive: false,
                visible: true,
                key: 'checkPhone'
            },
            {
                name: '手机校验',
                isActive: false,
                visible: false,
                key: 'print'
            },
            {
                name: '完成提交',
                isActive: false,
                visible: true,
                key: 'complete'
            }
        ]);
    }
};
</script>
