import request from '@/utils/request';

// 户政业务----证明类的 合肥 (注销户口证明、亲属关系证明、临时身份证明)

// 保存人脸识别
export const saveFaceReco = (params) => {
    return request.ajax('/ssm/zw/population/provingFaceRecognition', 'post', params);
};

// 获取无违法犯罪的理由
export const getNoCrimeReasons = (params) => {
    return request.ajax('/ssm/zw/population/wfzUsages', 'get', params);
};

//
export const getProvingResult = (params) => {
    return request.ajax('/ssm/zw/population/getProvingResult', 'post', params);
};

// 银川获取身份证照片
export const getIdCardPhotoServer = (params) => {
    return request.ajax('/ssm/zw/population/preCheck', 'post', params);
};

// 提交完成接口
export const proveComplete = (params) => {
    return request.ajax('/ssm/zw/population/finishProving', 'post', params);
};

// 提交完成接口
export const uploadLetter = (params) => {
    return request.ajax('/ssm/zw/population/saveRecommendPhoto', 'post', params);
};

// ----------------合肥无犯罪证明-----------
// 无犯罪证明人脸识别
export const noCrimeProveCheck = (params) => {
    return request.ajax('/ssm/commonzwapi/precheck', 'post', params);
};
// 无犯罪证明人脸识别
export const noCrimeProveFace = (params) => {
    return request.ajax('/ssm/zw/population/noCriminalFaceRecognition', 'post', params);
};
// 无犯罪证明提交用途和手机号
export const pushProve = (params) => {
    return request.ajax('/ssm/zw/population/pushPersonInfo', 'post', params);
};
// 无犯罪证明办理完成
export const proveCrimeComplete = (params) => {
    return request.ajax('/ssm/zw/population/finish', 'post', params);
};
